body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    overflow-x: hidden;
    background-color: #001F33;
}

.App {
    background-color: #001F33;
    height: 100%;
    /*color: white;*/
}

.fade-appear,
.fade-enter {
    opacity: 0.2;
    z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in-out 150ms;
    transition: opacity 300ms ease-in-out 150ms;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0.2;
    background-color: #001F33;
    -webkit-transition: opacity 150ms ease-in-out;
    transition: opacity 150ms ease-in-out;
}


/*Button*/

.Button {
    padding: 1rem 2.5rem;
    background-color: rgb(15, 63, 75);
    border-radius: 2rem;
    margin: 1rem 0;
    text-transform: uppercase;
    font-size: 0.8rem;
    -webkit-transition: all .5s;
    transition: all .5s;
    color: white;

}


.Button:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    box-shadow: .5rem .5rem 2rem rgba(0, 0, 0, 0.2);
}

.Button:active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    box-shadow: none;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.NavBar {
  width: 100%;
  -webkit-transition: .3s;
  transition: .3s;
  position: fixed;
  z-index: 999;
  background-color: #001b2d;
}

.NavBar a {
  cursor: pointer;
}

.NavBar a:active~.NavBar {
  display: none;
}

.NavBar header {
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: white;
}

.NavBar .nav-container {
  width: 90%;
  margin: auto;
}

.NavBar .nav-checkbox {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  padding: 1rem 0;
}



.NavBar .tab-nav {
  outline: none;
  display: none;
}

.NavBar .tab-nav-label {
  display: none;
  color: #fff;
}

.NavBar .tab-content {
  grid-column: 3;
  list-style: none;
  margin: 0;
  padding: 0;
}

.NavBar .tab-content li {
  display: inline-block;
  padding-right: 1rem;
}

.NavBar .tab-content li a {
  text-decoration: none;
  color: black;
  position: relative;
}


@media screen and (max-width: 1280px) {
  .NavBar .nav-container {
    width: 100%;
  }

}

@media screen and (max-width: 768px) {
  .NavBar .tab-nav-label {
    display: grid;
    cursor: pointer;
    grid-column: 3;
    padding-right: 1rem;
  }

  .NavBar .tab-content {
    display: grid;
    grid-template-columns: 1fr;
    grid-column: 1/4;
    text-align: center;
    max-height: 0;
    overflow: hidden;
    transition: all ease-in-out 0.4s;
    -o-transition: all ease-in-out 0.4s;
    -webkit-transition: all ease-in-out 0.4s;
  }

  .NavBar input:checked~.tab-content {
    max-height: 400px;
  }

  .NavLink li:active~.tab-content {
    max-height: 0;
  }

  .NavBar .tab-content li {
    padding: 1rem 0;
  }
}

.cta {
  color: #fff;
