  .Events {
  background-color: #001F33;
  height: 100%;
  color: white;
  background-image: url('../Events/bg.jpg');
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  color: white;
  font-family: 'Lato', sans-serif;
  position: relative;
  }


.Events h1 {
  font-size: 10vw;
  font-weight: 100;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  transition: .3s all ease;
}


.eventsSect {
  width: 100%;
  height: 100vh;
  background-color: #001F33;
}
